import "../../scss/layout/layout.scss";

(() => {
    //#region 导航菜单
    const parseMenu = (menuArr: any) => {
        let navItemArr = [];
        for (let i = 0; i < menuArr.length; i++) {
            let menuItem = menuArr[i];
            let navItem: string;
            if (menuItem.sub && menuItem.sub.length > 0) {

                let subNavItemArr = [];
                let sub = menuItem.sub;
                let subId = menuItem.subId;

                for (let j = 0; j < sub.length; j++) {
                    var menuSubItem = sub[j];
                    let subItem =
                        `<li class="nav-item">
    <a class="nav-link" href="${menuSubItem.href}">${menuSubItem.title}</a>
</li>`;
                    subNavItemArr.push(subItem);
                }

                let subNav =
                    `<div class="collapse" id="${subId}">
    <ul class="nav flex-column sub-menu">
       ${subNavItemArr.join(" ")}
    </ul>
</div>`;
                navItem =
                    `<li class="nav-item">
    <a class="nav-link collapsed" data-toggle="collapse" href="#${subId}" aria-expanded="false" aria-controls="ui-basic">
        <i class="${menuItem.icon}"></i>
        <span class="menu-title">${menuItem.title}</span>
        <i class="menu-arrow"></i>
    </a>
    ${subNav}
</li>`;

            } else {
                navItem = `<li class="nav-item">
                        <a class="nav-link" href="${menuItem.href}">
                            <i class="${menuItem.icon}"></i>
                            <span class="menu-title">${menuItem.title}</span>
                        </a>
                    </li>`;
            }
            navItemArr.push(navItem);
        }
        $("#sidebar>.nav").html(navItemArr.join(" "));
        (<any>window).activeMenu();
    }

    function getMenu() {
        $.get("/api/BackCmsHome/GetMenu",
            msg => {
                parseMenu(msg);
            });
    }
    //#endregion

    getMenu();
})();